import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { checkMembership } from '../services/api';
import axios from 'axios';

export default function MemberCheck() {
  const [nom, setNom] = useState('');
  const [prenom, setPrenom] = useState('');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const navigate = useNavigate();

  // Préchargement des sons
  const [buzzerSound] = useState(new Audio('/assets/buzzer.mp3'));
  const [bellSound] = useState(new Audio('/assets/bell.mp3'));

  // Vérification adhésion
  const handleVerification = async (e) => {
    e.preventDefault();
    
    if (!nom.trim() || !prenom.trim()) {
      setError("Veuillez entrer un nom et prénom valides");
      return;
    }
    
    setLoading(true);
    setError('');
    
    try {
      const result = await checkMembership(nom, prenom);
      
      console.log("Résultat de vérification:", result);
      
      if (result.success && result.isPaid) {
        // Enregistrement de la présence
        try {
          await axios.post('http://localhost:4000/presences', {
            type: 'adherent',
            nom,
            prenom,
            membreId: result.membre?.id || result.membre?.membreId
          });
        } catch (err) {
          console.error("Erreur d'enregistrement de présence:", err);
        }
        
        // Redirection vers la page de confirmation
        setTimeout(() => {
          navigate('/confirmation');
        }, 100);
      } else {
        // Jouer le son d'erreur
        buzzerSound.currentTime = 0;
        buzzerSound.play().catch(err => console.error("Erreur lecture audio:", err));
        
        // Afficher le message d'erreur
        setError(result.message || "Erreur lors de la vérification de l'adhésion");
      }
    } catch (err) {
      console.error("Erreur de vérification:", err);
      
      // Jouer le son d'erreur
      buzzerSound.currentTime = 0;
      buzzerSound.play().catch(err => console.error("Erreur lecture audio:", err));
      
      setError(err.message || "Erreur lors de la vérification");
    } finally {
      setLoading(false);
    }
  };

  // Appel bénévole
  const handleCallVolunteer = () => {
    bellSound.currentTime = 0;
    bellSound.play().catch(err => console.error("Erreur lecture audio:", err));
  };

  return (
    <div className="member-check">
      <h2>Vérification d'adhésion</h2>
      <form onSubmit={handleVerification}>
        <div className="form-group">
          <label htmlFor="nom">Nom</label>
          <input
            id="nom"
            type="text"
            placeholder="Nom"
            value={nom}
            onChange={(e) => setNom(e.target.value)}
            disabled={loading}
            required
          />
        </div>
        
        <div className="form-group">
          <label htmlFor="prenom">Prénom</label>
          <input
            id="prenom"
            type="text"
            placeholder="Prénom"
            value={prenom}
            onChange={(e) => setPrenom(e.target.value)}
            disabled={loading}
            required
          />
        </div>
        
        <button 
          type="submit" 
          disabled={loading}
          className={loading ? 'loading-button' : ''}
        >
          {loading ? 'Vérification en cours...' : 'Vérifier'}
        </button>
        
        {error && (
          <div className="error-message">
            {error}
          </div>
        )}
      </form>
      
      <div className="buttons-container">
        <button 
          onClick={() => navigate('/')}
          className="back-button"
        >
          Retour à l'accueil
        </button>
        <button 
          onClick={handleCallVolunteer}
          className="call-button"
        >
          Appeler un bénévole
        </button>
      </div>
    </div>
  );
}
